.contactSection {
  width: 100vw;
  position: relative;
  background: var(--background-color);
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contactSection::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--primary-color);
  clip-path: polygon(-10% 100%, 100% 0, 100% 100%, 0 100%);
  z-index: 1;
}

.contactSection .contactContainer {
  position: relative;
  display: flex;
  margin-bottom: 120px;
  z-index: 10;
  width: var(--site-width);
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(50px);
  padding: 72px;
  border: 2px solid #0a0d170d;
  border-radius: 20px;
}

.contactSection .contactContainer .contactLeftContainer {
  position: relative;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 40px;
}

.contactSection .contactContainer .contactLeftContainer .contactForm {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 14px;
}

.contactSection .contactContainer .contactLeftContainer .contactForm input,
.contactSection .contactContainer .contactLeftContainer .contactForm textarea {
  padding: 14px;
  border-radius: 5px;
  border: 1px solid #ffffff33;
  background: #25252366;
  outline: none;
  color: var(--secondary-color);
  font-size: 14px;
}

.contactSection .contactContainer .contactLeftContainer .contactForm textarea {
  resize: none;
  height: 200px;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .sendButton {
  text-align: center;
  background: var(--background-color);
  padding: 15px 0;
  color: var(--primary-color);
  border-radius: 5px;
  border: 2px solid #303030;
  cursor: pointer;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .sendButton.disabled {
  cursor: default;
  /* background: rgb(44, 44, 44);
  opacity: 0.9;
  color: rgb(189, 189, 189); */
  opacity: 0.8;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .sendButton.disabled:hover
  .buttonIcon {
  display: none;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .sendButton
  .buttonIcon {
  visibility: hidden;
  opacity: 0;
  margin-left: -20px;
  transition:
    opacity 0.3s 0.1s ease-in-out,
    margin 0.3s ease-in-out;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .sendButton:hover
  .buttonIcon {
  visibility: visible;
  opacity: 1;
  margin-left: 12px;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  input::placeholder,
.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  textarea::placeholder {
  color: #ffffff99;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .inlineInputs {
  position: relative;
  display: flex;
  gap: 14px;
}

.contactSection
  .contactContainer
  .contactLeftContainer
  .contactForm
  .inlineInputs
  input {
  width: 100%;
}

.contactSection .contactContainer .contactRightContainer {
  position: relative;
  height: fit-content;
  align-self: center;
  width: 50%;
  padding: 40px;
}

.contactSection .contactContainer .contactRightContainer .contentContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--background-color);
  border-radius: 10px;
  padding: 32px;
  gap: 32px;
}

.contactSection
  .contactContainer
  .contactRightContainer
  .contentContainer
  .contentTopContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contactSection
  .contactContainer
  .contactRightContainer
  .contentContainer
  .contentBottomContainer {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contactSection
  .contactContainer
  .contactRightContainer
  .contentContainer
  .contentBottomContainer
  .contentElement {
  display: flex;
  align-items: center;
  gap: 20px;
  color: var(--primary-color);
  font-size: 15px;
}

.contactSection
  .contactContainer
  .contactRightContainer
  .contentContainer
  .contentBottomContainer
  .contentElement:hover
  .elementLabel {
  text-decoration: underline;
}

.contactSection
  .contactContainer
  .contactRightContainer
  .contentContainer
  .contentBottomContainer
  .contentElement
  .elementIcon {
  font-size: 30px;
  width: 30px;
}

@media only screen and (max-width: 1200px) {
  .contactSection .contactContainer {
    flex-direction: column;
    align-items: center;
    width: fit-content;
    padding: 40px 50px;
    margin: 0 200px;
    margin-bottom: 70px;
    gap: 50px;
  }

  .contactSection .contactContainer .contactLeftContainer {
    align-items: center;
    text-align: center;
    gap: 15px;
  }

  .contactSection .contactContainer .contactLeftContainer,
  .contactSection .contactContainer .contactRightContainer {
    width: fit-content;
    padding: 0 100px;
  }

  .contactSection
    .contactContainer
    .contactLeftContainer
    .contactForm
    .inlineInputs {
    flex-direction: column;
  }
}

@media only screen and (max-width: 1100px) {
  .contactSection .contactContainer {
    margin: 0 100px;
    margin-bottom: 70px;
  }
}
